/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize();
        @include qodefButtonSimple();
        @include qodefButtonAfter(simple);
        cursor: pointer;
        z-index: 3;
    }
    
    a.button.added {
        display: none;
    }

    input[type="submit"],
    button[type="submit"]:not(.qodef-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include qodefButtonDefaultStyle();
        @include qodefButtonSize();
        @include qodefButtonOutlineColor();
        @include qodefButtonAfter();
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include qodefButtonOutlineHoverColor();
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */