/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .qodef-onsale,
    .qodef-out-of-stock,
    .qodef-new-product {
        @include qodefWooCommerceMarksGeneralStyle();
    }
    
    .qodef-onsale {
        @include qodefWooCommerceSaleStyle();
    }
    
    .qodef-out-of-stock {
        @include qodefWooCommerceOutOfStockStyle();
    }

    .qodef-new-product {
        @include qodefWooCommerceNewProductStyle();
    }
}

.qodef-woo-single-page {
    
    .qodef-single-product-content {
        
        .qodef-onsale {
            left: 20px;
            bottom: calc(20% + 5px);

            @include phone-landscape{
                bottom: calc(50% + 10px);
            }
        }
        
        .qodef-out-of-stock{

        }

        .qodef-new-product {
            right: 10px;
            bottom: calc(20% + 5px);

            @include phone-landscape{
                bottom: calc(50% + 10px);
            }
        }
    }
    
    &.qodef-woo-single-thumb-on-left-side {
        
        .qodef-single-product-content {
            
            .qodef-onsale {
                left: 130px;
                bottom: 0;

                @include phone-landscape{
                    bottom: calc(50% + 10px) !important;
                }
            }
            
            .qodef-out-of-stock{
                right: 0;
            }

            .qodef-new-product {
                right: 0;
                bottom: 0;

                @include phone-landscape{
                    bottom: calc(50% + 10px) !important;;
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */