/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.qodef-woocommerce-page .qodef-content {
    
    .variations {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;

        tbody{
            display: block;
            color: $default-heading-color;
        }
        
        tr {
            display: inline-block;
            width: 33%;
            padding: 0;
            border: 0;
            box-sizing: border-box;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            display: block;
            padding: 0;
            text-align: initial;
	        
	        &.label {
                font-weight: 700;
                margin-bottom: 3px;
	        }
	        
	        &.value {
		        white-space: nowrap;
		        
		        select {
			        width: auto;
			        min-width: 180px;
		        }

                .select2{
                    background-color: $additional-background-color;
                    font-family: $additional-text-font;
                    font-size: 12px;
                }
	        }
        }
        
        .reset_variations {
	        margin: 0 0 0 15px;
            color: $default-heading-color;
        }
    }
    
    .single_variation_wrap {
        margin: 6px 0 0;
        
        .single_variation {
            margin: 0 0 35px;
        }
    }
    
    table.group_table {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tbody {
            display: block;
        }
        
        tr {
            display: block;
            padding: 0 0 10px;
            border: 0;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            padding: 0 17px 10px 0;
            text-align: left;
            
            &.price {
                display: table-cell;
                vertical-align: middle;
            }
            
            &:last-child {
                padding: 0 0 10px;
            }
        }
        
        .qodef-quantity-buttons {
            margin: 0;
        }
        
        a {
            color: $default-text-color;
            
            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */