/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;
    padding-right: 7px;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.qodef-header-cart-icon-pack {
            
            .qodef-cart-icon {
                font-size: 19px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        .qodef-cart-number{
            position: absolute;
            top: 45%;
            @include qodefTransform(translateY(-50%));
            left: 85%;
            width: 15px;
            height: 15px;
            background-color: $first-main-color;
            border-radius: 50%;
            font-size: 10px;
            font-weight: 700;
            text-align: center;
            line-height: 15px;
            color: #fff;
        }

        &.qodef-header-cart-svg-path {
            
            .qodef-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }

    .qodef-cart-number{
        background-color: $first-main-color;
        color: #fff;
    }


}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 309px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include qodefTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 5px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 74px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        padding-left: 13px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: 0;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: block;
            margin: 3px 0 0;
            font-size: 15px;
            font-weight: 700;
            color: $first-main-color;
        }
    }

    .qodef-cart-bottom {
        padding: 5px 0 0;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 11px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .qodef-total{
                font-size: 12px;
                font-weight: 600;
                text-transform: uppercase;
                letter-spacing: .1em;
            }

            .qodef-total-amount {
                text-align: right;
                font-size: 16px;
                font-weight: 700;
                text-transform: uppercase;
                letter-spacing: .1em;
                color: $first-main-color;
            }
        }

        .qodef-btn-holder {
            font-size: 0;

            a {
                width: 50%;
                display: inline-block;
                height: 53px;
                line-height: 53px;
                font-size: 10px;
                font-weight: 600;
                letter-spacing: .1em;
                text-transform: uppercase;
                color: #fff;
                text-align: center;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
            }

            .qodef-view-cart{
                background-color: #111111;

                &:hover{
                    background-color: $first-main-color;
                }

            }

            .qodef-view-checkout{
                background-color: #252525;

                &:hover{
                    background-color: $first-main-color;
                }
            }

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */