/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();
	
	&.qodef-info-on-image {
		
		.qodef-pli {
			
			.qodef-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .qodef-pli {
        padding: 0;
        z-index: 100;

	    .qodef-pli-title {
			margin: 0;
			padding: 0;
			width: 60%;
			float: left;
			line-height: 24px;
	    }

		.qodef-pli-price {
			@include qodefWooCommercePriceStyle();
			text-align: right;
			margin: 0;
			padding: 0;
			width: 40%;
			float: right;
			line-height: 24px;
			font-weight: 700;
			font-size: 18px;
		}
	
	    .qodef-pli-category {
		    margin: 11px 0 3px;
			font-size: 12px;
			text-transform: uppercase;
			font-weight: 500;
			letter-spacing: .1em;
	    }

        .qodef-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 3px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }

    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {

            .qodef-pli-text {
                opacity: 1;
			}
			
			.qodef-pli-image img {
				@include qodefTransform(scale(1.04));
			}
        }

        .touch & {
            cursor: pointer;
		}

        .qodef-pli-image {
			@include qodefRelativeHolderLayout();
			overflow: hidden;

			img {
				display: block;
				width: 100%;
				backface-visibility: hidden;
				@include qodefTransition(.6s);
				
			}

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
		        @include qodefWooCommerceNewProductStyle();
	        }
        }

        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba($default-heading-color, 0.6);
            box-sizing: border-box;
            @include qodefTransition(opacity .2s ease-out);
        }

        .qodef-pli-text-outer {
	        @include qodefTableLayout();
	        text-align: center;
        }

        .qodef-pli-text-inner {
            @include qodefTableCellLayout();

            .qodef-pli-title,
            .qodef-pli-category a,
            .qodef-pli-excerpt,
            .qodef-pli-rating,
            .qodef-pli-price {
                color: #fff;
            }

	        .qodef-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }
        }

	    .qodef-pli-link {
		    @include qodefAbsoluteHolderLayout();
	    }
    }

    .qodef-pli-text-wrapper {
        @include qodefRelativeHolderLayout();
        margin: 32px 0 16px;

		.qodef-pli-add-to-cart {

			&.qodef-default-skin {

				.button,
				.added_to_cart {

					&:hover {
						color: $default-heading-color !important;
					}
				}
			}

			&.qodef-light-skin {

				.button,
				.added_to_cart {
					color: $default-heading-color;
					background-color: #fff;

					&:hover {
						color: #fff;
						background-color: $first-main-color;
					}
				}
			}

			&.qodef-dark-skin {

				.button,
				.added_to_cart {
					color: #fff;
					background-color: $default-heading-color;

					&:hover {
						color: #fff;
						background-color: $first-main-color;
					}
				}
			}
		}

		.button,
		.added_to_cart {
			@include qodefButtonDefaultStyle();
			@include qodefButtonSize();
			white-space: nowrap;
			z-index: 3;
			padding:0 !important;
		}

		.added_to_cart {
			&:after {
				font-family: ElegantIcons;
				content: "\24";
				margin: 0 0 0 12px;
				line-height: inherit;
				vertical-align: bottom;
				font-size: 1.25em
			}
		}

		.button.added {
			display: none;
		}
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: #fff;
				}
				
				.qodef-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner {
			
			.qodef-pli-text-inner {
				
				.qodef-pli-title,
				.qodef-pli-category a,
				.qodef-pli-excerpt,
				.qodef-pli-rating,
				.qodef-pli-price {
					color: $default-heading-color;
				}
				
				.qodef-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */