/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.qodef-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        color: #8d8c8c;
        display: inline;
        margin: 0 0 23px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: $sselect-input-height;
        width: 66.66%;
    }

    .woocommerce-ordering {
        text-align: right;
        width: 33.33%;
        padding-left: 15px;
        box-sizing: border-box;

        .orderby {
            width: auto;
            min-width: 303px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */