/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {
		
		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 141;
					$space_between_items: 20;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 54%;
		    margin: 0;
		    padding: 0 15px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 46%;
        padding: 0 0 0 15px;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: inline-block;
		    margin: -3px 0 12px;
	    }
	
	    .woocommerce-product-rating {
		    line-height: 1;
		    margin: 0;
			float: right;
			text-align: right;

			a.woocommerce-review-link{
				display: block;
				margin: 11px 0 0;
				font-weight: 300;
				font-size: 14px;
				font-family: $additional-text-font;
			}
	    }

		.price {
			@include qodefWooCommercePriceStyle();
			margin: 0;
			font-size: 20px;
			font-weight: 700;
		}
	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
		    margin: 30px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 49px 0 0;

		
		    > span {
			    display: block;
			    margin: 5px 0;
				font-weight: 700;
				color: $default-heading-color;

				> a,
				> span {
					font-weight: 300;
					color: $default-text-color;
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }

		button[type=submit]:not(.qodef-search-submit){
			font-size: 10px;
			border: 1px solid $default-border-color;
			line-height: 51px;
            height: 50px;
			padding: 0 21px !important;
		}
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 33px 0 0;
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 15px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 55px 0 0;
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 2px solid #efefef;
			text-align: left;
			clear: both;

			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				bottom: -2px;
				padding-right: 4%;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 16px 0;
					font-size: 18px;
                    line-height: 1.3em;
					font-weight: 700;
					letter-spacing: -.025em;
					text-transform: capitalize;
					border-bottom: 2px solid transparent;
					@include qodefTransition(all .2s ease);

				}

				&.active{
					a{
						padding: 16px 22px;
						border-color: $first-main-color;
					}
				}

				&:last-child{
					padding-right: 0;
				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
            padding-top: 28px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 36px 0 0;
			}
		}
		
		table {
			margin: 36px 0 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 36px 0 0;
			
			h2 {
				font-size: 19px;
				line-height: 24px;
				margin: 0 0 37px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 44px;
					background-color: #fafafa;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefTableLayout();
					table-layout: initial;
					padding: 34px;
					box-sizing: border-box;

					img {
						float: left;
						width: 150px;
						border-radius: 50%;

						@media screen and (max-width: 480px){
							display: none;
						}
					}

					.comment-text {
						@include qodefTableCellLayout();
						width: 88%;
						padding: 0 0 0 34px;

						@media screen and (max-width: 480px){
							width: 100%;
							padding: 0;
						}

						.star-rating {
							line-height: 27px;
							float: right;
							text-align: right;
						}

						p {
							margin: 0;

							.woocommerce-review__author{
								font-size: 19px;
								font-weight: 700;
								color: $default-heading-color;
								display: block;
							}

							.woocommerce-review__dash{
								display: none;
							}

							.woocommerce-review__published-date{
								font-size: 12px;
								font-family: $additional-text-font;
								color: #a1a1a1;
							}
						}

						.description{

							p{
								margin: 10px 0;
							}
						}

					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					font-size: 19px;
					font-weight: 700;
					color: $default-heading-color;
					display: block;
					margin: 85px 0 27px;
				}
				
				p {
					margin: 0;
				}

				.comment-notes{
					font-style: italic;
					margin: 0 0 27px;
				}

				label{
					font-size: 14px;
					font-weight: 600;
					color: $default-heading-color;
					letter-spacing: -.025em;
				}
				
				.stars {
					overflow: hidden;
					margin: 0 0 28px;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();
					}
				}

				.form-submit{

					input{
						line-height: 58px;
						border: 1px solid $default-border-color;
						padding: 0 28px;
						@include qodefTransition(all .2s ease-in-out);

						&:hover{
							border-color: $first-main-color;
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			margin: 37px 0;
			text-align: left;
		}
		
		.product {
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}

body.qodef-woo-single-page .select2-container--default.select2-container--open .select2-dropdown {
	border: 1px solid #e1e1e1;
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */