/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include qodefRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .qodef-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {

        margin: 0 0 50px;

        .qodef-widget-title {
            margin: 0 0 28px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #000;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 9px;
                    width: 9px;
                    border-radius: 50%;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include qodefTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 29px 0 0;
            
            .button {
                float: right;
                line-height: 22px;
                @include qodefButtonSimple();

                &:hover {
                    color: $default-heading-color !important;
                    background-color: transparent !important;
                    border: none !important;
                }

            }
            
            .price_label {
                float: left;
                font-weight: 700;
                line-height: 22px;
                color: #000;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 2px 0;

                a{
                    font-size: 12px;
					text-transform: uppercase;
					font-weight: 700;
                    letter-spacing: .1em;

                }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .qodef-widget-title {
            margin: 0 0 25px;
        }
        
        ul {
            
            li {
                padding-top: 9px;
                min-height: 74px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                        font-weight: 700;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 14px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;

                    span {
                        color: inherit;
                    }
                }

                del{
                    .amount{
                        font-weight: 700;
                        font-size: 14px;
                    }
                }
                
                ins {
                    text-decoration: none;
                    font-weight: 700;
                    margin-top: 5px;

                    .amount {
                        font-weight: 700;
                        color: $first-main-color;
                    }
                }

                > .amount:nth-child(4){
                    display: block;
                }

                .amount{
                    font-weight: 700;
                }
            }
        }
    }

    &.widget_recent_reviews {

        ul {

            li {

                a {

                    span {
                        padding: 0;
                    }
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 4px;
                    margin-bottom: 6px;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 14px !important;
                
                &:after {
                    content: '/';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;
            
            > div {
                @include qodefRelativeHolderLayout();
                box-sizing: border-box;
                background-color: $additional-background-color;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 60px;
                line-height: 32px;
                color: $default-heading-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: $additional-text-font;
                font-size: 12px;
    
                @include qodefPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 19px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */