/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: #282828;
            background-color: $additional-background-color;
            border: 0;
            text-align: center;
            box-sizing: border-box;
        }

        .qodef-quantity-input {
            position: relative;
            height: 52px;
            width: 43px;
            padding: 0;
            margin: 0 25px 0 0;
            font-family: $additional-text-font;
            font-size: 12px;
            line-height: 52px;
            outline: 0;
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            color: #8d8c8c;
            right: 0;
            width: 26px;
            height: 26px;
            line-height: 26px;
            border-left: 0;
            cursor: pointer;
            font-size: 18px;
            @include qodefTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-quantity-plus {
            top: 0;
            border-bottom: 0;
            line-height: 32px;
        }

        .qodef-quantity-minus {
            bottom: 0;
            line-height: 20px;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */